import React from 'react'
import { NavLink } from 'react-router-dom'
import { List, ListItem, ListItemText, Divider, Icon, Collapse } from '@material-ui/core'
import { match as createMatch } from 'path-to-regexp2'

import routes from '../../configs/routes'
import logo from '../../images/logo.png'

import './nav.scss'

const isSystem = window.localStorage.getItem('type') === 'system'
let initOpen = false 


const Link = ({ path, children, parent, isActive, i, toggleOpen }) => {
  const className = parent ? 'parent' : 'children'

  const handleIsActive = (match, location) => {
    const result = isActive ? !!createMatch(isActive)(location.pathname) : !!match

    if (!initOpen && result) {
      toggleOpen(i)
      initOpen = true
    }

    return result
  }

  return path === undefined ? (
    <div className={className}>
      {children}
    </div>
  ) : (
    <NavLink exact to={path} className={className} isActive={handleIsActive}>
      {children}
    </NavLink>
  )
}

const LinkListItem = ({ i, path, label, hidden, parent, isActive, toggleOpen }) => {
  return !hidden && (
    <Link path={path} parent={parent} isActive={isActive} i={i} toggleOpen={toggleOpen}>
      <ListItem button dense onClick={() => parent && !path && toggleOpen(i)}>
        <ListItemText primary={label} />
      </ListItem>
    </Link>
  )
}

const Nav = () => {
  const [open, setOpen] = React.useState(null)

  const toggleOpen = (i) => {
    setOpen(initOpen && open === i ? null : i)
  }

  React.useEffect(() => {
    initOpen = false
  }, [])


  return (
    <nav className="nav">
      <div className="logo">
        <img src={logo} alt="logo" />
      </div>

      <List>
        {routes.map((item, i) => !item.hidden && (!item.system || (item.system && isSystem)) && (
          <div className="group" key={i}>
            <LinkListItem {...item} parent toggleOpen={toggleOpen} i={i} />

            {Boolean(item.children?.length) && (
              <>
                <Collapse in={open === i} timeout="auto">
                  {item.children.map((item2, i2) => (!item2.system || (item2.system && isSystem)) && (
                    <LinkListItem {...item2} key={i2} toggleOpen={toggleOpen} i={i} />
                  ))}
                </Collapse>

                <Icon className="expandIcon">{open && open === i ? 'expand_less' : 'expand_more'}</Icon>
              </>
            )}

            <Divider />
          </div>
        ))}
      </List>
    </nav>
  )
}

export default Nav