import React, { useEffect } from 'react'
import { format } from 'date-fns'

import '../components/queryTrace/queryTrace.scss'
import API from '../api/Api'

import Table from '../components/table/Table'
import DefaultTemplate from '../components/template/DefaultTemplate'


const PostingLookup = () => {
  const [data, setData] = React.useState([])

  const columns = [
    {
      field: 'created_at',
      title: '발급시간',
      render: rowData => rowData.created_at ? format(new Date(rowData.created_at), 'yyyy-MM-dd HH:mm:dd') : ''
    },
    {
      field: 'org_name',
      title: '발송기관'
    },
    {
      field: 'dept_name',
      title: '발송부서'
    },
    {
      field: 'reg_dt',
      title: '분석날짜',
      render: rowData => format(new Date(rowData.reg_dt), 'yyyy-MM-dd HH:mm:dd')
    },
    {
      field: 'name',
      title: '수신자명'
    },
    {
      field: 'template_code',
      title: '서식 코드'
    },
    {
      field: 'doc_title',
      title: '문서 제목'
    },
    {
      field: 'dm_type',
      title: '우편 구분'
    },
    {
      field: 'dm_reg_num',
      title: '등기번호',
    },
    {
      field: 'disp_class',
      title: '발송종류'
    },
    {
      field: 'disp_status',
      title: '발송상태'
    },
    {
      field: 'request_id',
      title: '접수번호'
    },
    {
      field: 'groupId',
      title: '묶음발송 신청일'
    },
    {
      field: 'is_readed',
      title: '우편열람여부'
    },
    {
      field: 'read_at',
      title: '우편열람시간',
      render: rowData => rowData.read_at ? format(new Date(rowData.read_at), 'yyyy-MM-dd HH:mm:dd') : ''
    }
  ]

  const init = async () => {
    const res = await API.getCertHistory()

    setData(res.map(item => ({
      id: item.id,
      created_at: item.created_at,
      ...JSON.parse(item.data)
    })))
  }

  useEffect(() => {
    init()
  }, [])


  return (
    <DefaultTemplate>
      <Table
        scrollable
        term
        termColumn="created_at"
        title="수발신 증명서 발급 이력"
        columns={columns}
        data={data}
        options={{
          filtering: true,
          exportButton: true
        }}
      />
    </DefaultTemplate>
  )
}

export default PostingLookup