import React, { useState, useEffect } from 'react'
import { format } from 'date-fns'

import API from '../api/Api'

import DefaultTemplate from '../components/template/DefaultTemplate'
import Table from '../components/table/Table'

const PostingStatistics = () => {
  const [data, setData] = useState([])

  const columns = [
    /* {
      field: 'idx',
      title: '번호'
    }, */
    {
      field: 'reg_dt',
      title: '접수일'
    },
    {
      field: 'org_name',
      title: '기관명'
    },
    {
      field: 'dept_name',
      title: '부서명'
    },
    {
      field: 'reg_cnt',
      title: '접수물량',
      filtering: false
    },
    {
      field: 'done_dm_cnt',
      title: '종이우편 발송물량',
      filtering: false
    },
    {
      field: 'done_app_cnt',
      title: '모바일우편 발송물량',
      filtering: false
    },
    {
      field: 'done_mms_cnt',
      title: '문자우편 발송물량',
      filtering: false
    },
    {
      field: 'result_mms_fail_cnt',
      title: 'MMS 발송실패 건수'
    },
    {
      field: 'deny_cnt',
      title: '차단건수'
    }
  ]

  const init = async () => {
    const res = await API.getPostStatistics()

    setData(res.map(item => ({
      ...item,
      reg_dt: format(new Date(item.reg_dt), 'yyyy-MM-dd')
    })))
  }

  useEffect(() => {
    init()
  }, [])

  return (
    <DefaultTemplate>
      <Table
        scrollable
        term
        termColumn="reg_dt"
        title="우편물 발송 통계"
        columns={columns}
        data={data}
        options={{
          filtering: true,
          exportButton: true
        }}
      />
    </DefaultTemplate>
  )
}

export default PostingStatistics